/* WEBSITE */
*{margin:0; padding:0}

html {
	height:90%;
  font-family: 'Roboto Mono', monospace;
}

body {
    height: 100%;
    background-image: url("Background.png");
    background-position: center;
    background-size: cover;
}


#navbar {
    background-color: #ECEFF4;
    height: 8vh;
    width: 100%;
    color: #3B4252;
    font-family: 'Roboto Mono', monospace;
    font-weight: 500;
    box-shadow: 0px 10px 30px 3px rgba(0, 0, 0, 0.25);
}

/* NAVBAR */
#logo {
    width: 8%;
    padding-left: 1%;
    padding-top: 0.75%;
}

.links {
    list-style-type: none;
    display: inline-block;
}

.links li {
    float: left;
  }

.links li a {
    color: #2E3440;
    display: block;
    font-size: 110%;
    text-align: center;
    padding: 2vh 2vw;
    text-decoration: none;
  }

  .navButton {
    background-color: #8FBCBB;
    font-family: 'Roboto Mono', monospace;
    border-radius: 20px;
    font-weight: bold;
    border: none;
    color: #2E3440;
    font-weight: bold;
    padding: 13px 32px;
    text-align: center;
    font-size: 16px;
    margin: 1% 1%;
    cursor: pointer;
    
  }
.navButton {
  float: right;
}

.addExpense {
  float: right;
 
}


.navButton:hover {
    background-color: #5E81AC;
    transition: 0.5s;
}

/* BODY WRAP */
#bodyWrap {
  margin: 3% 6%;
  border-radius: 10px;
  padding: 2% 4%;
  background-color: #E5E9F0;
  box-shadow: 0px 10px 30px 3px rgba(0, 0, 0, 0.25);
}

/* OVERVIEW */
#overview {
  /* background-color: white; */
  height: 50px;
  padding: 30px 0px;
}

.overviewBox {
  padding: 20px 50px;
  border-radius: 10px;
  margin: 10px 50px 10px 0px;
  /* background-color: red; */
  display: inline;
  font-weight: bold;
  text-align: center;
}

#myBudgetBox {
  background-color: #D8DEE9;
}

#myRemainingBox {
  background-color: #98D988;
}

#mySpentBox {
  background-color: #FFABAB;
}